<!-- 推荐生成任务管理 -->

<template>
  <div class="taskManagement">
    <!-- <div class="left">
      <div class="left-header">
        <el-input class="input" placeholder="输入关键字进行过滤" v-model="filterText"></el-input>
        <span class="el-icon-plus icon" @click="addNewDirectory"></span>
      </div>
      <el-tree
        class="filter-tree"
        :data="treeData"
        :props="defaultProps"
        default-expand-all
        :filter-node-method="filterNode"
        @node-click="nodeClick"
        ref="tree"
      >
        <span class="slot-t-node item" slot-scope="{ node, data }">
          <em :class="data.icon"></em>
          <span class="item-text">{{ node.label }}</span>
          <div class="item-icon-group">
            <i @click="editNewDirectory" class="item-icon el-icon-edit"></i>
            <i @click="delNewDirectory" class="item-icon el-icon-delete"></i>
          </div>
        </span>
      </el-tree>
    </div>-->
    <div class="right">
      <div class="right-button">
        <el-button type="primary" @click="createTask">新增推荐生成任务</el-button>
      </div>
      <div class="right-content">
        <bdaTable
          class="content-table"
          :tableData="taskData"
          :headerColor="taskHeaderColor"
          :indexWidth="0"
          :isPage="true"
          :pageConfig="pageConfig"
          @operate="taskOperate"
          @changePage="changePage"
        ></bdaTable>
      </div>
      <el-dialog
        class="addNew-dialog"
        :title="addNewDialogTitle"
        :visible.sync="addNewDialogVisible"
        width="70%"
        :before-close="addNewDialogClose"
        :close-on-click-modal="false"
        :close-on-press-escape="false"
      >
        <span slot="title" class="dialog-header">
          <span class="header-text">{{ "新增" }}</span>
        </span>
        <div class="dialog-content">
          <el-form v-model="addNewForm" label-width="120px">
            <el-row>
              <el-col :span="8">
                <el-form-item label="目录编码：">
                  <el-input size="mini" class="input" v-model="addNewForm.directoryCode"></el-input>
                </el-form-item>
              </el-col>
              <el-col :span="8">
                <el-form-item label="目录级数：">
                  <el-input size="mini" class="input" v-model="addNewForm.directoryLevel"></el-input>
                </el-form-item>
              </el-col>
              <el-col :span="8">
                <el-form-item label="目录排序号：">
                  <el-input size="mini" class="input" v-model="addNewForm.directorySort"></el-input>
                </el-form-item>
              </el-col>
            </el-row>
            <el-row>
              <el-col :span="24">
                <el-form-item label="标签目录名称：">
                  <el-input size="mini" class="input-row" v-model="addNewForm.labelDirectoryName"></el-input>
                </el-form-item>
              </el-col>
            </el-row>
            <el-row>
              <el-col :span="24">
                <el-form-item label="目录描述：">
                  <el-input
                    type="textarea"
                    :rows="4"
                    class="input-row"
                    placeholder="请输入内容"
                    v-model="addNewForm.directoryDescription"
                  ></el-input>
                </el-form-item>
              </el-col>
            </el-row>
          </el-form>
        </div>
        <span slot="footer" class="dialog-footer">
          <el-button type="primary" @click="addNewSubmit">提交</el-button>
        </span>
      </el-dialog>
      <operateDialog
        v-if="createTaskDialogVisible"
        :visible.sync="createTaskDialogVisible"
        :showData="createTaskDialogData"
        :type="createTaskDialogType"
        @operateStauts="operateStauts"
      ></operateDialog>
    </div>
  </div>
</template>

<script>
import Interface from '@/base/mixin/recommend-management';
import bdaTable from "@/components/bda-table/bda-table";
import operateDialog from "./components/operate-dialog";

export default {
  components: {
    bdaTable,
    operateDialog
  },
  name: "taskManagement",
  mixins: [Interface],
  watch: {
    filterText(val) {
      this.$refs.tree.filter(val);
    }
  },
  props: {
    menuType: {
      type: String,
      default: ''
    }
  },
  data() {
    return {
      //过滤文本
      filterText: "",
      //树数据
      treeData: [],
      defaultProps: {
        children: "children",
        label: "label"
      },
      //新增目录
      addNewDialogVisible: false,
      addNewDialogTitle: '',
      addNewDialogStatus: '',
      addNewForm: {
        directoryCode: "",
        directoryLevel: "",
        directorySort: "",
        labelDirectoryName: "",
        directoryDescription: ""
      },
      //推荐生成任务
      taskData: {},
      taskHeaderColor: "",
      //查询
      queryForm: {
        current: 1,
        size: 10,
      },
      //分页设置
      pageConfig: {
        current: 1,
        size: 10,
        pageSizes: [10, 20, 30, 40],
        layout: "prev, pager, next, sizes,jumper,total",
        total: 0
      },
      //推荐任务
      createTaskDialogVisible: false,
      createTaskDialogData: {},
      createTaskDialogType: "add",
      //更新周期中文
      updateCycleOptions: [
        { name: "指定日期", value: '0' },
        { name: "日更新", value: '1' },
        { name: "周更新", value: '2' },
        { name: "月更新", value: '3' },
        { name: "年更新", value: '4' },
      ]
    };
  },
  mounted() {
    this.getTreeData();
    this.getTaskData();
  },
  methods: {
    //点击树节点
    nodeClick(obj, node) {
      console.log(obj, node, "obj, node");
    },
    //增加新目录
    addNewDirectory() {
      this.addNewDialogTitle = '新增目录'
      this.addNewDialogStatus = 'add'
      this.addNewDialogVisible = true;
    },
    //编辑目录
    editNewDirectory(event) {
      if (event.currentTarget === event.target) {
        this.addNewDialogTitle = '编辑目录'
        this.addNewDialogStatus = 'edit'
        this.addNewDialogVisible = true;
      }

    },
    //删除目录
    delNewDirectory() { },
    //点击提交
    addNewSubmit() { },
    //关闭对话框
    addNewDialogClose() {
      this.addNewDialogVisible = false;
    },
    //保存后
    operateStauts() {
      this.getTaskData()
    },
    //改变分页
    changePage(data) {
      this.queryForm.size = data.size
      this.queryForm.current = data.current
      this.getTaskData()
    },
    //新增推荐生成任务
    createTask() {
      this.createTaskDialogVisible = true;
      this.createTaskDialogType = "add";
    },
    //点击任务表格
    taskOperate(type, data) {
      console.log(type, data);
      this.createTaskDialogType = type;
      if (type === 'edit') {
        this.getTaskDetail(data)
      }
      if (type === 'detail') {
        this.getTaskDetail(data)
      }
      if (type === 'delete') {
        this.deleteTaskList(data)
      }
    },
    //删除推荐任务
    deleteTaskList(data) {
      this.$confirm('是否删除该条数据?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        closeOnClickModal: false,
        closeOnPressEscape: false,
        confirmButtonClass: 'btnFalses',
        type: 'warning'
      }).then(() => {
        this.deleteRecommendGenerateTask({ relationNumber: data.relationNumber, tagId: data.tagId }).then(res => {
          this.$message.success(res)
          this.queryForm.current = 1
          this.queryForm.size = 10
          this.getTaskData()
        })
      }).catch(() => {
      });
    },
    //树过滤时触发
    filterNode(value, data) {
      console.log(value, data, "value, data");
      if (!value) return true;
      return data.label.indexOf(value) !== -1;
    },
    //

    //获取树数据
    getTreeData() {
      this.selectDirectoryNavigation({}).then(res => {
        console.log(res, 'selectDirectoryNavigation')
      })
      this.treeData = [
        {
          id: 1,
          label: "一级 1",
          icon: "el-icon-folder-opened",
          children: [
            {
              id: 4,
              label: "二级 1-1",
              children: [
                {
                  id: 9,
                  label: "三级 1-1-1"
                },
                {
                  id: 10,
                  label: "三级 1-1-2"
                }
              ]
            }
          ]
        },
        {
          id: 2,
          label: "一级 2",
          icon: "el-icon-folder-opened",
          children: [
            {
              id: 5,
              label: "二级 2-1"
            },
            {
              id: 6,
              label: "二级 2-2"
            }
          ]
        },
        {
          id: 3,
          label: "一级 3",
          icon: "el-icon-folder-opened",
          children: [
            {
              id: 7,
              label: "二级 3-1"
            },
            {
              id: 8,
              label: "二级 3-2"
            }
          ]
        }
      ];
    },
    //获取推荐生成任务列表
    getTaskData() {
      this.taskHeaderColor = "#1899fa";
      let columnNameData = [
        { prop: "relationNumber", label: "关系编号", width: 120 },
        { prop: "relationName", label: "关联关系名称" },
        { prop: "descibe", label: "关联关系描述" },
        // { prop: "updateCycle", label: "更新周期" },
        { prop: "updateCycleName", label: "更新周期" },
        { prop: "createTime", label: "创建时间" },
        { prop: "updateTime", label: "更新时间" },
        { prop: "exeTime", label: "执行时间" },
        {
          prop: "Operate",
          label: "操作",
          align: "center",
          // width: 120,
          type: [
            { name: "编辑", action: "edit" },
            { name: "详情", action: "detail" },
            { name: "删除", action: "delete" },
          ]
        }
      ]

      this.selectRecommendGenerateTaskList({ ...this.queryForm, menuType: this.menuType }).then(res => {
        this.pageConfig.total = res.total
        this.pageConfig.size = this.queryForm.size
        this.pageConfig.current = this.queryForm.current
        res.records.forEach(e => e.updateCycleName = this.translate(e.updateCycle, this.updateCycleOptions))
        this.taskData = {
          tableData: [
            ...res.records
          ],
          columnNameData: columnNameData
        };
      }).catch(err => {
        if (err) {
          this.taskData = {
            tableData: [],
            columnNameData: columnNameData
          }
        }
      })
    },
    //获取详情
    getTaskDetail(data) {
      this.selectRecommendGenerateTaskDetail(
        {
          relationNumber: data.relationNumber,
          targetId: data.targetId,
          recId: data.recId,
          tagId: data.tagId
        }).then(res => {
          this.createTaskDialogData = res;
          this.createTaskDialogVisible = true;
        })
    },
    //转义表格
    translate(item, data) {
      let name = ''
      data.forEach(e => {
        if (e.value === item) {
          name = e.name
        }
      })
      return name
    }
  }
};
</script>
  <style >
.btnFalses {
  color: #fff !important;
}
</style>
<style scoped lang="scss">
.taskManagement {
  width: 100%;
  height: 100%;
  display: flex;
  .filter-tree {
    .item {
      width: 100%;
      // display: flex;
      // align-items: center;
      position: relative;
      &:hover .item-icon-group {
        display: inline-block;
      }
    }
    .item-text {
    }
    .item-icon-group {
      position: absolute;
      right: 20px;

      display: none;
      .item-icon {
        margin-right: 10px;
        &:hover {
          color: #1899fa;
        }
      }
    }
  }

  .left {
    width: 20%;
    margin-right: 20px;
    background: #ffffff;
    border: 1px solid #e6e6e6;
    padding: 10px;
    box-sizing: border-box;
    ::v-deep .el-tree-node__content:hover {
      background-color: #f2f8ff;
    }
    .left-header {
      display: flex;
      align-items: center;
      .input {
        width: 90%;
      }
      .icon {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 10%;
        font-size: 16px;
        color: #1a84f4;
        cursor: pointer;
      }
    }
  }
  .right {
    ::v-deep .el-table .warning-row {
      background: #e9f3fe;
    }
    width: calc(100%);
    // padding: 10px;
    // box-sizing: border-box;
    .right-button {
      margin: 10px;
    }
    .right-content {
      width: 100%;
      height: calc(100% - 60px);
      background: #ffffff;
      // border: 1px solid #e6e6e6;
      padding: 10px;
      box-sizing: border-box;

      .content-table {
        height: 100%;
      }
    }
  }
  .addNew-dialog {
    ::v-deep .el-dialog__header {
      padding: 10px 0 10px;
      border-bottom: 1px solid #e6e6e6;
    }
    ::v-deep .el-dialog__body {
      padding: 10px 20px;
    }
    ::v-deep .el-dialog__footer {
      padding: 10px 10px 10px 0;
      border-top: 1px solid #e6e6e6;
      text-align: center;
    }
    .dialog-header {
      .header-text {
        margin-left: 20px;
        color: #808080;
        font-size: 14px;
      }
    }
    .dialog-content {
      .input {
        width: 80%;
      }
      .input-row {
        width: 94.5%;
      }
    }
    .dialog-footer {
    }
  }
}
</style>
